.dropdown {
  position: relative; }

.dropdown__list {
  position: absolute;
  left: 0;
  overflow: visible;
  visibility: hidden;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 3px;
  opacity: 0;
  transition: .5s ease all;
  transform: translate(0, -8px); }
  .dropdown__list.dropdown__list--open {
    z-index: 10;
    visibility: visible;
    opacity: 1;
    transform: translate(0, 8px); }
  .dropdown__list:before {
    position: absolute;
    top: -6px;
    left: calc(50% - (15px/2));
    z-index: 10;
    width: 0;
    height: 0;
    content: ' ';
    border-color: transparent transparent #fff;
    border-style: solid;
    border-width: 0 7.5px 6px; }
  .dropdown__list:after {
    position: absolute;
    top: -7px;
    left: calc(50% - (15px/2));
    z-index: 9;
    width: 0;
    height: 0;
    content: ' ';
    border-color: transparent transparent #ccc;
    border-style: solid;
    border-width: 0 7.5px 6px; }

.dropdown__item {
  font-size: 1rem;
  white-space: nowrap; }
  .dropdown__item:not(:last-of-type) {
    border-bottom: 1px solid #ccc; }
  .dropdown__item a {
    display: block;
    padding: 5px 10px;
    color: #000;
    text-decoration: none; }
  .dropdown__item:hover {
