[data-tooltip],
[data-tooltip-top] {
  position: relative;
  z-index: 0; }
  [data-tooltip]:before,
  [data-tooltip-top]:before {
    position: absolute;
    top: -90%;
    left: 0%;
    display: block;
    width: 100%;
    padding: 0.50015rem 0.70721rem;
    margin: 0 auto;
    font-size: 0.70721rem;
    cursor: default;
    visibility: hidden;
    content: attr(data-tooltip);
    background-color: #bbb;
    border-radius: 3px;
    opacity: 0;
    transition: .5s ease all;
    transform: translate(0, 8px); }
  [data-tooltip]:after,
  [data-tooltip-top]:after {
    position: absolute;
    top: -20%;
    left: calc(50% - (15px / 2));
    z-index: 10;
    width: 0;
    height: 0;
    content: ' ';
    border-color: #bbb transparent transparent;
    border-style: solid;
    border-width: 6px 7.5px 0;
    opacity: 0;
    transition: .5s ease all;
    transform: translate(0, 8px); }
  [data-tooltip]:hover:before,
  [data-tooltip-top]:hover:before {
    visibility: visible;
    opacity: 1;
    transform: translate(0, 0); }
  [data-tooltip]:hover:after,
  [data-tooltip-top]:hover:after {
    visibility: visible;
    opacity: 1;
    transform: translate(0, 0); }

.modal__outer {
